<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="BossId" prop="boss_id" :required="true">
            <el-input v-model.number="requestParam.boss_id"></el-input>
          </el-form-item>
          <el-form-item label="AppId" prop="app_id" :required="true">
            <el-input v-model.number="requestParam.app_id"></el-input>
          </el-form-item>
          <el-form-item label="渠道码">
            <el-input v-model.number="requestParam.channel_code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="Search">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click="add" v-popover:popover1
              >新增</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
          header-row-class-name="center"
        >
          <el-table-column prop="node_id" label="节点id"  align="center"> </el-table-column>
          <el-table-column prop="sort" label="排序"  align="center"> </el-table-column>
          <el-table-column prop="type" label="节点类型"  align="center"> </el-table-column>
          <el-table-column prop="name" label="节点名称"  align="center"> </el-table-column>
          <el-table-column
            prop="icon"
            label="图标ID 或路径"
            :show-overflow-tooltip="true"
             align="center"
          >
          </el-table-column>
          <el-table-column label="操作"  align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="edit(scope.$index, scope.row.node_id), getNodeById()"
                >编辑</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.row.app_id, scope.row.node_id)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
      <template>
        <el-dialog
          :title="action.title"
          :visible.sync="dialogFormVisible"
          show-close:true
        >
          <el-form
            :label-position="labelPosition"
            label-width="100px"
            :model="node"
            class="formStyle text_common"
            size="mini"
            ref="node"
          >
            <el-form-item
              label="BossId"
              prop="boss_id"
              v-show="action.type === 'add'"
              :required="action.type === 'add'"
            >
              <el-input v-model.number="node.boss_id"></el-input>
            </el-form-item>
            <el-form-item label="设备Id" prop="app_id" :required="true">
              <el-input v-model.number="node.app_id"></el-input>
            </el-form-item>
            <el-form-item
              label="节点id"
              prop="node_id"
              v-show="action.type === 'edit'"
              :required="action.type === 'edit'"
            >
              <el-input v-model.number="node.node_id"></el-input>
            </el-form-item>
            <el-form-item
              label="排序"
              prop="sort"
              v-show="action.type === 'edit'"
            >
              <el-input v-model.number="node.sort"></el-input>
            </el-form-item>
            <el-form-item
              label="渠道码"
              prop="channel_code"
              v-show="action.type === 'add'"
              :required="action.type === 'add'"
            >
              <el-input v-model.number="node.channel_code"></el-input>
            </el-form-item>
            <el-form-item label="名称" prop="name" :required="true">
              <el-input v-model="node.name"></el-input>
            </el-form-item>
            <el-form-item label="图标" prop="icon">
              <el-input v-model.number="node.icon"></el-input>
            </el-form-item>
            <el-form-item label="类型" prop="type">
              <el-radio-group v-model="node.type" size="medium">
                <el-radio border :label="1">游戏</el-radio>
                <el-radio border :label="2">其他</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="状态" prop="status">
              <el-radio-group v-model="node.status" size="medium">
                <el-radio border :label="0">下架</el-radio>
                <el-radio border :label="1">正常</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="游戏版本" prop="game_version">
              <el-input v-model="node.game_version"></el-input>
            </el-form-item>
            <el-form-item label="游戏ID" prop="game_id">
              <el-input v-model.number="node.game_id"></el-input>
            </el-form-item>
            <el-form-item label="房间ID" prop="room_id">
              <el-input v-model.number="node.room_id"></el-input>
            </el-form-item>
            <el-form-item label="房间列表" prop="roomIds">
              <el-input
                type="textarea"
                :rows="2"
                class="dialog_input"
                placeholder="用逗号隔开，比如：123,457,457"
                v-model="arraystr.room_list"
                auto-complete="false"
              >
              </el-input>
            </el-form-item>
            <el-form-item label="桌子ID" prop="desk_id">
              <el-input v-model.number="node.desk_id"></el-input>
            </el-form-item>
            <el-form-item label="匹配类型" prop="queue_type">
              <el-input v-model.number="node.queue_type"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" size="medium" @click="AddOrEdit"
                >提交</el-button
              >
              <el-button type="danger" size="medium" @click="resetForm('node')"
                >重置</el-button
              >
            </el-form-item>
          </el-form>
        </el-dialog>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        app_id: "",
        channel_code: "",
        boss_id: ""
      },
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      showTab: false,
      action: {
        type: "",
        title: ""
      },
      node: {
        boss_id: "", //y number
        app_id: "", //y number
        node_id: "",
        sort: "",
        channel_code: "", //y number
        name: "", //y
        icon: "", //n number
        type: "", //n number
        status: 1, //n number 0:下架 1：正常
        game_version: "", //n
        game_id: "", //n number
        room_id: "", //n number
        room_list: [], //n
        desk_id: "", //n number
        queue_type: "" //n number
      },
      arraystr: {
        room_list: ""
      },
      dialogFormVisible: false,
      formLabelWidth: "20%"
    };
  },
  methods: {
    Search() {
      var that = this;
      that.responseData = [];
      this.$api.game.nodeList(this.requestParam).then(res => {
        if (res.data.success === true) {
          that.responseData = res.data.data.node;
          that.page.total = that.responseData.length;
          const start = (that.page.current_page - 1) * that.page.page_size;
          const end =
            that.page.total > that.page.current_page * that.page.page_size
              ? that.page.current_page * that.page.page_size
              : that.responseData.length;
          that.page.list = that.responseData.slice(start, end);
        } else {
          this.$message({
            showClose: true,
            message: "获取节点列表失败",
            type: "error"
          });
        }
      });
    },
    AddOrEdit() {
      var that = this;
      that.node.room_list = that.arraystr.room_list
        .replace(/\s/g, "")
        .replace(/，/g, ",")
        .split(",")
        .map(Number);
      if (this.action.type === "add") {
        this.$api.game.addNode(this.node).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "新增节点成功",
              type: "success"
            });
            that.resetForm();
            that.dialogFormVisible = false;
          } else {
            this.$message({
              showClose: true,
              message: "新增节点失败",
              type: "error"
            });
          }
        });
      } else {
        this.$api.game.updateNode(this.node).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "修改节点成功",
              type: "success"
            });
            that.resetForm();
            that.dialogFormVisible = false;
            that.Search();
          } else {
            this.$message({
              showClose: true,
              message: "修改节点失败",
              type: "error"
            });
          }
        });
      }
    },
    resetForm() {
      this.node = {
        boss_id: "",
        app_id: "",
        node_id: "",
        sort: "",
        channel_code: "",
        name: "",
        icon: "",
        type: "",
        status: 1,
        game_version: "",
        game_id: "",
        room_id: "",
        room_list: [],
        roomIds: "",
        desk_id: "",
        queue_type: ""
      };
      this.arraystr.room_list =''
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    add() {
      this.action.type = "add";
      this.action.title = "新增节点";
      this.resetForm();
      this.dialogFormVisible = true;
    },
    edit(index, node_id) {
      // if (this.showTab === false) {
      //   this.showTab = true;
      //   this.action.type = "edit";
      //   this.action.title = "更新节点";
      //   this.node.node_id = node_id;
      //   this.node.app_id = this.requestParam.app_id;
      //   this.node.boss_id = this.requestParam.boss_id;
      //   this.node.channel_code = this.requestParam.channel_code;
      // }
      this.action.type = "edit";
      this.action.title = "更新节点";
      this.resetForm();
      this.node.node_id = node_id;
      this.node.app_id = this.requestParam.app_id;
      this.node.boss_id = this.requestParam.boss_id;
      this.node.channel_code = this.requestParam.channel_code;
      this.dialogFormVisible = true;
    },
    handleDelete(app_id, node_id) {
      this.$confirm("确定要删除吗？", "删除").then(action => {
        this.$api.game
          .deleteNode({ app_id: this.requestParam.app_id, node_id: node_id })
          .then(res => {
            if (res.data.success === true) {
              this.$message({
                showClose: true,
                message: "删除节点成功",
                type: "success"
              });
              var i = this.responseData.findIndex(item => {
                if (item.node_id === node_id) {
                  return true;
                }
              });
              this.responseData.splice(i, 1);
              this.page.total = this.responseData.length;
              const start = (this.page.current_page - 1) * this.page.page_size;
              const end =
                this.page.total > this.page.current_page * this.page.page_size
                  ? this.page.current_page * this.page.page_size
                  : this.responseData.length;
              this.page.list = this.responseData.slice(start, end);
            } else {
              this.$message({
                showClose: true,
                message: "删除节点失败",
                type: "error"
              });
            }
          });
      });
    },
    getNodeById() {
      let that = this;
      this.$api.game.getNodeById({ node_id: this.node.node_id }).then(res => {
        if (res.data.success === true && res.data.data.info) {
          that.resetForm();
          that.node = res.data.data.info;
          that.arraystr.room_list = res.data.data.info.room_list.toString();
        }
      });
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.page.list = this.responseData.slice(
        (this.page.current_page - 1) * this.page.page_size,
        this.responseData.length > this.page.current_page * this.page.page_size
          ? this.page.current_page * this.page.page_size
          : this.responseData.length
      );
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    }
  },
  watch: {
    //   action: {
    //     //深度监听，可监听到对象、数组的变化
    //     handler(val, oldVal) {
    //       console.log(val);
    //       if (val.type == "edit") {
    //         this.getNodeId();
    //       }
    //     },
    //     immediate: true,
    //     deep: true //true 深度监听
    //   }
    // },
    // dialogFormVisible(val) {
    //   if (val == false) {
    //     this.action = {
    //       type: "",
    //       title: ""
    //     };
    //   }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.editTable {
  z-index: 2004;
  position: absolute;
  top: 100px;
  left: 500px;
  background-color: #f1f1f1;
  width: 900px;
  opacity: 1;
}
.need {
  color: red;
  font-size: 20px;
  font-weight: 600;
}
</style>



